const List = props => (
    <ul>
        <table>
        {
            props.items.map((item,index) => 
                <tr>
                    <li key={index}>
                        <td style={tableStyle}>{item}</td>
                        <td><button onClick={this.props.handleDelete.bind(null, item)} key={index}>Delete</button></td>
                    </li>
                </tr>
            )
        }
        </table>
    </ul>
)

export default List